import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';


function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <Link color="inherit" href="https://material-ui.com/">
                Your Website
        </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = makeStyles((theme) => ({
    typography: {
        fontFamily: [
            '"Helvetica Neue"',
        ].join(','),
    },
    mainFeaturedPost: {
        position: 'relative',
        backgroundColor: theme.palette.grey[800],
        color: theme.palette.common.white,
        backgroundImage: 'url(https://live.staticflickr.com/65535/51241554333_5c51a5d0df_o.jpg)',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        borderRadius: '0px',
        opacity: '0.9',
        boxShadow: '0px 0px 0px 0px'
    },
    mediaTitle: {
        fontFamily: "Abhaya Libre",
        textTransform: 'uppercase',
        fontStyle: 'italic',
        letterSpacing: '25px',
        color: '#fff',
        paddingTop: theme.spacing(70),
        textShadow: '0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%)'
    },
    mediaSubTitle: {
        opacity: '0.7',
        color: '#fff',
        paddingBottom: theme.spacing(15)
    },
    heroContent: {
        padding: theme.spacing(4, 0, 0),
    },
    galleryGrid: {
        paddingTop: theme.spacing(8),
        paddingBottom: theme.spacing(8),
    },
    imgContainer: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        width: '100%'
    },
    imgMedia: {
        paddingBottom: '36.25%', // 16:9
        width: '100%',
        objectFit: 'cover'
    },
    cardContent: {
        flexGrow: 1,
    },
    galleryContent: {
        padding: theme.spacing(0, 0, 4),
        opacity: '0.7'
    },
    footer: {
        backgroundColor: theme.palette.background.paper,
        padding: theme.spacing(6),
    },
}));

export default function MediaGallery() {

    const classes = useStyles();
    const [items, setItems] = useState([]);
    const [galleryTitle, setGalleryTitle] = useState('');

    useEffect(() => {
        fetch('https://api.flickr.com/services/rest/?method=flickr.photosets.getPhotos&api_key=dd4fba161f787299af518125880e903f&photoset_id=72157719398466589&user_id=153252113@N08&extras=url_o,url_l&format=json&nojsoncallback=1')
            .then((response) => response.json())
            .then((data) => {
                console.log(data);
                setItems(data.photoset.photo);
                setGalleryTitle(data.photoset.title);
            });

        //setSelectedImage(imageURL(resp.data.photos.photo[0]));

    }, [setItems])

    return (
        <React.Fragment>
            <CssBaseline />
            <main>
                {/* Hero unit */}
                <div className={classes.heroContent}>
                    <Paper className={classes.mainFeaturedPost}>
                        <Container maxWidth="md">
                            <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom className={classes.mediaTitle}>
                                {galleryTitle}
                            </Typography>
                            <Typography component="p" align="center" className={[classes.mediaSubTitle, classes.typography].join(" ")}>
                                45.2631°N, -122.7318°W
                            </Typography>
                        </Container>
                    </Paper>
                </div>
                <Container className={classes.galleryGrid} maxWidth="sm">
                    <Typography component="p" align="left" className={[classes.galleryContent, classes.typography].join(" ")}>

                    </Typography>
                </Container>
                <Container className={classes.galleryGrid} maxWidth="md">
                    <Grid container spacing={4}>
                        {items.map((item, index) => (
                            <Grid item key={index} xs={12} sm={12} md={12}>
                                <div className={classes.imgContainer}>
                                    <img src={imageURL(item)} className={classes.imgMedia} />
                                </div>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
            </main>
            {/* Footer */}
            <footer className={classes.footer}>
                <Typography variant="h6" align="center" gutterBottom>
                    Woah!
                </Typography>
                <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
                    If you made it this far, Thank you! Enter discount code <b>SummerJam21</b> on my store for 50% off
                </Typography>
                <Copyright />
            </footer>
            {/* End footer */}
        </React.Fragment>


    )
}

// assemble image URL
function imageURL(item: any) {

    return item.url_l


    // thumbnail
    //return 'http://farm' + item.farm + '.staticflickr.com/' + item.server + '/' + item.id + '_' + item.secret + '.jpg'
}